<template>
    <div class="col-lg-3 col-md-4">
        <h5 class="section-title position-relative text-uppercase mb-3"><span class="bg-secondary pr-3">Filter by Brand</span></h5>
            <div class="bg-light p-4 mb-30">
                <form>
                    <div class="custom-control custom-checkbox d-flex align-items-center justify-content-between mb-3">
                        <input type="checkbox" class="custom-control-input" checked id="price-all">
                        <label class="custom-control-label" for="price-all">All Price</label>
                    </div>
                    
                </form>
        </div>
        <div v-if="stage==='all'">
            <h5 class="section-title position-relative text-uppercase mb-3"><span class="bg-secondary pr-3">Filter by Category</span></h5>
            <div class="bg-light p-4 mb-30">
                <form>
                    <!-- <button @click="applyFilter">Apply Filter</button> -->
                    <div v-for="item in data" :key="item.id" class="custom-control custom-checkbox align-items-center justify-content-between mb-1">
                        <input type="checkbox" class=""  :value="item.id" v-model="selectedCategory" :id="item.id" @change="applyFilterCategory">
                        <label style="padding:10px" class="" for="">{{item.pd_cs_name}}</label>
                    </div>
                    
                </form>
            </div>
        </div>

    </div>
</template>

<script>
import axios from 'axios';
import dataServe from '../../data';
export default {
  name: 'SlideBar',
  data() {
    return {
      data:'',
      selectedCategory: []
    };
  },
  props: {
    stage: String
  },
  mounted () {
        this.getCategories();
    },
    methods: {
        getCategories(){
            axios.post(dataServe.server,{
                action:"categories",
                }).then(res=>{
                this.data=res.data;
                })

        },
        applyFilterCategory() {
            // Emit an event with the selected categories
            this.$emit('filter-applied-category', this.selectedCategory);
        }

    }

}
</script>

