<template>
<div class="container-fluid">
        <div class="row px-xl-5">
            
            <SlideBar @filter-applied-category="filterItems" :stage="this.stage"/>

            <!-- Shop Product Start -->
            <div class="col-lg-9 col-md-8">
                <div class="row pb-3">
                    <div class="col-12 pb-1">
                        <div class="d-flex align-items-center justify-content-between mb-4">
                            <div>
                                <button class="btn btn-sm btn-light"><i class="fa fa-th-large"></i></button>
                                <button class="btn btn-sm btn-light ml-2"><i class="fa fa-bars"></i></button>
                            </div>
                            <div class="ml-2">
                                <!-- <div class="btn-group">
                                    <button type="button" class="btn btn-sm btn-light dropdown-toggle" data-toggle="dropdown">Sorting</button>
                                    <div class="dropdown-menu dropdown-menu-right">
                                        <a class="dropdown-item" href="#">Latest</a>
                                        <a class="dropdown-item" href="#">Popularity</a>
                                        <a class="dropdown-item" href="#">Best Rating</a>
                                    </div>
                                </div>
                                <div class="btn-group ml-2">
                                    <button type="button" class="btn btn-sm btn-light dropdown-toggle" data-toggle="dropdown">Showing</button>
                                    <div class="dropdown-menu dropdown-menu-right">
                                        <a class="dropdown-item" href="#">10</a>
                                        <a class="dropdown-item" href="#">20</a>
                                        <a class="dropdown-item" href="#">30</a>
                                    </div>
                                </div> -->
                            </div>
                        </div>
                    </div>



                    <div v-for="item in filteredItems" :key="item.id" class="col-lg-4 col-md-6 col-sm-6 pb-1">
                        <div class="product-item bg-light mb-4">
                            <div class="product-img position-relative overflow-hidden">
                                <img class="img-fluid w-100" src="" alt="">
                                <div class="product-action">
                                    <a class="btn btn-outline-dark btn-square" href=""><i class="fa fa-shopping-cart"></i></a>
                                    <a class="btn btn-outline-dark btn-square" href=""><i class="far fa-heart"></i></a>
                                    <a class="btn btn-outline-dark btn-square" href=""><i class="fa fa-sync-alt"></i></a>
                                    <a class="btn btn-outline-dark btn-square" href=""><i class="fa fa-search"></i></a>
                                </div>
                            </div>
                            <div class="text-center py-4">
                                <router-link :to="{ name: 'productD', params: { id: item.id } }" >{{ item.pd_name }}</router-link>
                                <div class="d-flex align-items-center justify-content-center mt-2">
                                    <h5>$123.00</h5><h6 class="text-muted ml-2"><del>$123.00</del></h6>
                                </div>
                                <div class="d-flex align-items-center justify-content-center mb-1">
                                    <!-- <small class="fa fa-star text-primary mr-1"></small>
                                    <small class="fa fa-star text-primary mr-1"></small>
                                    <small class="fa fa-star text-primary mr-1"></small>
                                    <small class="fa fa-star text-primary mr-1"></small>
                                    <small class="fa fa-star text-primary mr-1"></small>
                                    <small>(99)</small> -->
                                </div>
                            </div>
                        </div>
                    </div>


                    
                    <div class="col-12">
                        <nav>
                          <ul class="pagination justify-content-center">
                            <li class="page-item"><a class="page-link" @click="getPagePrevious">previous</a></li>
                            <li class="page-item"><p>&nbsp;&nbsp; {{page+1}} &nbsp;&nbsp;</p></li>
                            <li class="page-item"><a class="page-link" @click="getPageNext">Next</a></li>
                          </ul>
                        </nav>
                    </div>
                </div>
            </div>
            <!-- Shop Product End -->
        </div>
    </div>
</template>

<script>
import axios from 'axios';
import SlideBar from './shopcomponents/SlideBar.vue';
import dataServe from '../data';
export default {
  name: 'AllProduct',
  props: {
    stage: String
  },
  components: {
    SlideBar
  },
  data() {
    return {
      data:'',
      datapage:'',
      page:0,
      lengthData:0,
      typeQ:this.stage,
      selectedCategory: []
    };
  },
  created() {
        this.getProducts();
        this.getProductsPage();
        // setInterval(this.getProducts, 1000)
    },
    computed: {
        filteredItems() {
        if (this.selectedCategory.length === 0) {
            return this.datapage;
        } else {
            return this.data.filter(data => this.selectedCategory.includes(data.pd_category_id));
        }
        }
    },
    methods: {
        getProducts(){
                console.log(this.typeQ)
                axios.post(dataServe.server,{
                action:"getProduct",
                stage:this.typeQ
                }).then(res=>{
                this.data=res.data;
                this.lengthData=this.data.length
                })
        },
        getProductsPage(){
            axios.post(dataServe.server,{
            action:"getProductPage",
            page:this.page
            }).then(res=>{
            this.datapage=res.data;
            })
        },
        getPageNext(){
            if(this.lengthData>12 && this.page<this.lengthData/12-1){
                this.page++

                axios.post(dataServe.server,{
                    action:"getProductPage",
                    page:this.page*12
                    }).then(res=>{
                    this.datapage=res.data;
                })
                
            }
        },
        getPagePrevious(){
            if(this.page>0){
                this.page = this.page-1
                axios.post(dataServe.server,{
                    action:"getProductPage",
                    page:this.page*12
                    }).then(res=>{
                    this.datapage=res.data;
                })
            }
        },
        filterItems(categories) {
         this.selectedCategory = categories;
        }


    },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
a{
    cursor: pointer;
}
p{
    text-align: center;
    font-size: 20px;
}
</style>
