<template>
  <div class="container-fluid pt-5" style="padding-left: 100px; padding-right: 100px;">
        <h2 class="section-title position-relative text-uppercase mx-xl-5 mb-4"><span class="bg-secondary pr-3">Categories</span></h2>
        <div class="row px-xl-5 pb-3">

            <div v-for="item in data" :key="item.id" class="col-lg-3 col-md-4 col-sm-6 pb-1">
                <router-link class="text-decoration-none" :to="{ name: 'shop', params: { stage:item.id} }">
                    <div class="cat-item d-flex align-items-center mb-4" >
                        <div class="overflow-hidden" style="width: 70px; height: 70px;">
                            <img class="img-fluid" src="" alt="">
                        </div>
                        <div class="flex-fill pl-3">
                            <h6>{{ item.pd_cs_name }}</h6>
                            <small class="text-body"></small>
                        </div>
                    </div>
                </router-link>
            </div>


        </div>
    </div>
</template>

<script>
import axios from 'axios';
import dataServe from '../data';
export default {
    
  name: 'CateGoies',
  data() {
    return {
      data:''
    };
  },
  mounted() {
        this.getCategories();
    },
    methods: {
        getCategories(){
            axios.post(dataServe.server,{
                action:"categories"
                }).then(res=>{
                this.data=res.data;
                })
            }


    }
    
}
</script>

