import { createApp } from 'vue'
import App from './App.vue'
import { createRouter, createWebHistory } from 'vue-router';

import HomeIndex from './Home.vue'
import ProductD from './Product.vue'
import ShopIndex from './Shop.vue'

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeIndex
  },
  {
    path: '/shop/:stage',
    name: 'shop',
    component: ShopIndex
  },

  {
    path: '/product/:id',
    name: 'productD',
    component: ProductD
  }
];

const router = createRouter({
  history: createWebHistory(),
  routes,
  base: '/'
});


createApp(App).use(router).mount('#app');

